<script>
export default {
	name: 'apiDetail',
    data(){
        return{
            shows:0,
			selectId: 0,
			rightIndex: 1,
			showIndex: 1,
			activeName: 'first',
			oneTap: false,
			twoTap: false,
			threeTap: false,
			fourTap: false,
			fiveTap: false,
			sixTap: false,
			fixed: false,
			dimList: [],
			dimListDetail:{},
			showLeft: false,
			tableData: [{
				date1: 'keyword',
				date2: 'string',
				date3: '是',
				date4: '企业id/企业名称/统一信用代码/注册号'
			  }],
			leftLoading: false,
			result: {
				tmList: [
					{
						"regNo": "60618100",
						"tmName": "百度问一问",
						"tmPic": "https://tm-image.tianyancha.com/tm/822a4e0560502dd161421611dc5208ed.jpg",
						"appDate": "2021-11-16",
						"tmClass": "38",
						"id": '230424694',
						"intCls": "38-通讯服务",
						"applicantCn": "北京百度网讯科技有限公司",
						"category": "商标注册申请---申请收文",
					}]
				},
			quaryData: [],
			codeData: [
				{
					date1: '300000',
					date2: '无数据',  
				},
				{
					date1: '300001',
					date2: '请求失败',  
				},
				{
					date1: '300002',
					date2: '账号失效',  
				}
			],
			total: 0,
			detailObj: {},
			respExample: undefined
		}
    },
    methods:{
		changeTab(id) {
			this.showIndex = id
			this.$refs.container.scrollTop = 0
		},
		goMsg() {
			let targetbox= document.getElementById('msg');
			document.documentElement.scrollTop = targetbox.offsetHeight+120;
		},
		goWrong() {
			let targetbox= document.getElementById('topHeight');
			// // this.target= ;  
			// document.body.scrollTop  = targetbox.offsetTop
			// this.$refs['apiDetail'].scrollTo(0,200);
			document.documentElement.scrollTop = targetbox.offsetHeight+120;
		},
		goExample() {
			let targetbox= document.getElementById('topHeight');
			let targetbox1= document.getElementById('wrongcode');
			document.documentElement.scrollTop = targetbox.offsetHeight+targetbox1.offsetHeight+200
		},
		// listChange(index) {
		// 	console.log(index)
		// 	this.shows = index
			
		// },
		apply() {
			let user = this.$cookies.getJSON('user')
			if (user && user.userId) {
				this.$router.push('/center/case3?from=detail')
			} else {
				this.$store.commit('setShowLogin',1)
			}
		},
		testApi() {
			let user = this.$cookies.getJSON('user')
			if (user && user.userId) {
				this.$router.push(`/apiTest?dimId=${this.shows}&id=${this.selectId}`)
			} else {
				this.$store.commit('setShowLogin',1)
			}
		},
		handleClick(tab, event) {
			console.log(tab, event);
		},
		handleOpen(key, keyPath) {
			console.log(key, keyPath);
			this.shows = key
			// this.getDrop()
			// this.getRight(key,'',1)
	    },
	    handleClose(key, keyPath) {
		 console.log(key, keyPath);
	    },
		handleSelect(key, keyPath) {
		 let id = parseInt(key.split('-')[1])
		 this.getDetail(id)
		 this.initPage()
	    },
		initPage() {
			window.scrollTo(0,0);
			this.showIndex = 1
			this.$refs.container.scrollTop = 0
			this.oneTap = false
			this.twoTap = false
			this.threeTap = false
			this.fourTap = false
			this.fiveTap = false
			this.sixTap = false
			this.fixed = false
			this.$refs.code.scrollTop = 0
		},
		debounce(fn,wait){
		  var timer = null;
		  return function(){
		    if(timer !== null){
		      clearTimeout(timer);
		    }
		    timer = setTimeout(fn,wait);
		  }
		},
		handleScroll() {
			let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop // 滚动条偏移量
			if (scrollTop>168) {
				this.fixed = true
			} else {
				this.fixed = false
			}
		},
		getLeft() {
			this.leftLoading = true
			// 8027
			this.$axios.get('http://192.168.0.99:8020/portal/api/dim/interface/detail')
			  .then((res) =>{
				  console.log(res)
				  if (res.state===200) {
					
				  } else {
					this.$message.error(res.message);
				  }
				  this.dimList = res.data.item
				  this.total = res.data.total
				  this.resolveDimList(this.dimList)
				  this.showLeft = true
				  this.leftLoading = false
			  })
			  .catch(err=>{
				  this.$message.error(err);
				  this.leftLoading = false
			  })
		},
		resolveDimList(dimList) {
			dimList.forEach((item,index)=>{
				// this.getDrop(item.id,'',1,dimList.length)
				this.dimListDetail[item.dimId] = item.intfList
				// if (dimList.length===index+1) {
				// 	this.leftLoading=false
				// 	this.showLeft = true
				// }
			})
		},
		getDetail(id) {
			// 8027
			this.$axios.get('http://192.168.0.99:8020/portal/api/interface/details',{id: id})
			.then((res) =>{
			    console.log(res)
				this.detailObj = res.data
				try{
					this.tableData = JSON.parse(res.data.reqParamPretty)
					let arr = JSON.parse(res.data.respFiledDesc)
					arr.forEach((item,index)=>{
						Object.values(item).forEach(deom=>{
							if (deom instanceof Array) {
								arr[index]['children'] = deom
							}
						})
						// if (item.equityChangeRecordList) {
						// 	arr[index]['children'] = item.equityChangeRecordList
						// }
					})
					this.quaryData = arr
					console.log(this.quaryData)
					this.respExample = JSON.parse(res.data.respExample)
				}catch(e){
					console.log(e)
				}
				console.log(this.detailObj.respExample)
				this.codeData = res.data.respCodes
			})
			this.selectId = id
		}
    },
    mounted(){
		this.shows = this.$route.query.dimId
		this.selectId = this.$route.query.id
		this.getLeft()
		// this.getDimList()
		this.getDetail(this.selectId)
